@import "../../../styles/vars";
@import "../../../styles/mixins";

.aboutStaffSlider {
  &__container {
     text-align: center;
     margin: 47px -5px 87px;

    @include lgScreenMedia {
      margin: 36px -10px 66px;
    }

    @include smScreenMedia {
      margin: 15px -5px 40px;
    }
  }

  &__title {
    @include setDistance(margin-bottom, 60);
  }

  &__itemWrapper {
     padding-left: 20px;
     padding-right: 20px;
     height: 100%;

    @include xlScreenMedia {
      padding-left: 15px;
      padding-right: 15px;
    }

    @include lgScreenMedia {
      padding-left: 10px;
      padding-right: 10px;
    }

    @include smScreenMedia {
      padding-left: 5px;
      padding-right: 5px;
    }
  }

  &__itemBlock {
     height: 100%;
     border: 14px solid #124149;
     border-radius: 30px;
     overflow: hidden;

    @include xlScreenMedia {
      border-width: 12px;
      border-radius: 14px;
    }

    @include lgScreenMedia {
      border-width: 8px;
      border-radius: 16px;
    }

    @include smScreenMedia {
      border-width: 5px;
      border-radius: 15px;
    }
  }

  &__imgStaff {
    width: 100%;
  }

  &__deskWrapper {
    height: 100%;
    @include setFont(20px, 300);
    text-align: left;
    line-height: 23px;
    color: $color-white;
    background-color: #1c1c1c;
    padding: 20px 24px 33px 24px;

    @include xlScreenMedia {
      padding: 15px;
    }

    @include lgScreenMedia {
      @include setFont(14px, 300);
      line-height: 17px;
      padding: 8px;
    }
  }

  &__name {
    @include setFont(24px, 700);
    line-height: 28px;
    margin-bottom: 10px;

    @include xxlScreenMedia {
      @include setFont(22px, 700);
    }

    @include xlScreenMedia {
      @include setFont(20px, 700);
    }

    @include lgScreenMedia {
      @include setFont(17px, 700);
      line-height: 23px;
      margin-bottom: 7px;
    }

    @include smScreenMedia {
      @include setFont(15px, 700);
    }

    @include xsmScreenMedia {
      @include setFont(20px, 700);
    }
  }

  &__position {
    display: block;
    margin-bottom: 10px;

    @include xxlScreenMedia {
      @include setFont(20px, 400);
    }

    @include xlScreenMedia {
      @include setFont(18px, 400);
    }

    @include lgScreenMedia {
      @include setFont(16px, 400);
      line-height: 19px;
      margin-bottom: 11px;
    }

    @include smScreenMedia {
      @include setFont(15px, 400);
    }

    @include xsmScreenMedia {
      @include setFont(16px, 400);
    }
  }

  &__carousel {
    margin-bottom: 35px;
  }

  :global(.slick-list),
  :global(.slick-track) {
    display: flex !important;
  }

  :global(.slick-slide),
  :global(.slick-slide>div) {
    height: 100%;
  }
}